body {
	font-family: 'Roboto', sans-serif;
	@include f(15px,500,1.73);
	color: #5B5E77;
	@include font-smoothing;
	background-color: #fff;
	@include fadeout(.3s);
	&.is-loaded {
		@include fadein; } }


.title-main {
	@include fny;
	@include css-lock(30,100,1023,1440);
	letter-spacing: -0.01em;
	line-height: 0.83; }

.title {
	@include fny;
	color: #FFFFFF;
	letter-spacing: -0.36px;
	line-height: 1.4;
	@include css-lock(23,40,1023,1440); }

.flex-end {
	align-self: flex-end; }

.loader {
	position: fixed;
	top: 0;
	left: 0;
	z-index: 99999;
	width: 100%;
	height: 100vh; }
