//FONTS
@mixin fontpath($name, $name1, $file) {
  src: local($name), local($name1), url("../fonts/" + $file + ".woff2") format("woff2"), url("../fonts/" + $file + ".woff") format("woff"); }
@mixin fontdefault($style, $weight, $name) {
  font-family: $name;
  font-style: $style;
  font-weight: $weight; }

$NoirPro: 'Noir-Pro';
@mixin fnr {
  font-family: $NoirPro, sans-serif;
  font-weight: 500; }
@mixin fny {
  font-family: 'New York', sans-serif;
  font-weight: 500; }

@font-face {
  @include fontdefault(normal, 500, $NoirPro);
  @include fontpath('NoirPro-Medium', 'NoirPro-Medium', NoirPro-Medium); }

$timesBold: 'Times';
@mixin ftb {
  font-family: $timesBold, sans-serif; }

// Times-Bold
@font-face {
  @include fontdefault(normal, 700, $timesBold);
  @include fontpath('Times-Bold', 'Times-Bold', Times-Bold); }

